@keyframes rotate-watch-arrow {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  margin: 0;
  display: flex;
}

.watch {
  border: 5px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch__hour {
  transform-origin: 0;
  background-color: #00f;
  width: 24vmin;
  height: 2.4vmin;
  animation: 60s linear .5s infinite rotate-watch-arrow;
  position: absolute;
  top: 40vmin;
  left: 40vmin;
}

.watch__center {
  z-index: 1;
  background-color: orange;
  border-radius: 50%;
  width: 4vmin;
  height: 4vmin;
  position: absolute;
  top: 39.2vmin;
}

.watch__minute {
  transform-origin: bottom;
  background-color: green;
  width: 1.6vmin;
  height: 40vmin;
  animation: 5s linear .5s infinite rotate-watch-arrow;
  position: absolute;
  top: 0;
}

